body {
  margin: 0;
  padding: 0;
  font-family:  Roboto, "Helvetica Neue", Helvetica, "Hiragino Sans GB", STHeitiSC-Light, "Microsoft YaHei", 微软雅黑, Arial, sans-serif;
}
html {
	-webkit-text-size-adjust: 100%;
}
body, h1, h2, h3, h4, h5, h6, hr, p, blockquote, dl, dt, dd, ul, ol, li, pre, form, fieldset, legend, button, input, textarea, th, td,menu,nav,header{ margin:0; padding:0; }
table { border-spacing:0; }
a {color:black;text-decoration: none;}
h1, h2, h3, h4, h5, h6{ font-size:12px; font-weight:normal;}
ul,ol{list-style: none;}
img{display: block; border:0 none;}
input { font-family:  Roboto, "Helvetica Neue", Helvetica, "Hiragino Sans GB", STHeitiSC-Light, "Microsoft YaHei", 微软雅黑, Arial, sans-serif; border:0 none; outline-style:none;}
textarea { resize:none; } 
.clearfix:after{content:''; height:0; line-height:0; visibility:hidden; display:block; clear:both;}
.clearfix{zoom:1;}
.blocks-container>.container,.miners-container>.container,.tranning-container>.container,.Faq-container>.container{
	padding-bottom: 20px;
    
}


