.miners-container{
	background: white;
	padding-top: 70px;
	padding-bottom: 70px;
}
.minersDashboard {
	margin:20px 20px 0;
	position: relative;

}
.minersDashboard span {
	margin-right: 40px;
	line-height: 40px;
    
}
.minersDashboard .search-box {
	position: absolute;
    width: 346px;
    line-height: 40px;
    border-radius: 4px;
    background: #FAFAFA;
    border: 1px solid #CCCCCC;
    padding: 0 50px 0 15px;
    right: 0;
    font-size: 14px;
    outline: none;
}
.minersDashboard .btn-search {
	position: absolute;
    z-index: 5;
    width: 40px;
    height: 40px;
    background: url(../../imgs/search.png) no-repeat center;
    background-size: 24px;
    right: 0;
    top: 0;
    border: none;
    outline: none;
    cursor: pointer;
}
.minersDashboard .btn-search:disabled {
    cursor: not-allowed;
}
.minersList {
	margin: 40px 0 0;
	width: 1200px;
}
.minersList th,.minersList td {
	padding-left: 24px;
    padding-right: 24px;
    text-align: left;
    height: 50px;
    font-size: 16px;
    white-space: nowrap;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    line-height: 50px;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}
.minersList td {
	font-size: 16px;
}
.minersList tbody tr:nth-of-type(odd) {
	background-color: #F8F9FA;
}
.minersList a {
	color: #0D9FFF;
}
.minersList thead th:first-child {
	width: 40%;
}
.minersList thead th {
	width: 15%;
}


