.Moniter-info {
    margin-top: 20px;
    color:#333;
    border:1px solid rgb(202, 217, 234);
    padding: 20px;
    border-radius: 4px;
}
.Moniter-info>div {
    float: left;
    width: 50%;
    
}
.Moniter-info .left .address {
    line-height: 54px;
    font-size: 18px;
}
.Moniter-info .right>div {
    line-height: 27px;
    font-size: 16px;
}
.Moniter-info .right .space {
    display: inline-block;
}
.Moniter-info .right .space:first-of-type {
    width: 50%;

}
.Moniter-info .right .space:last-of-type {
    width: 50%;
    text-align: right;
}
.Moniter-detail {
    margin-top: 20px;
}
.Moniter-detail .title {
    text-align: center;
    margin-bottom: 15px;
    font-size: 16px;
}
.Moniter-detail>div {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 0px 20px;
}

.share-table,.hash-table
{
    border-collapse: collapse;
    margin: 0 auto;
    text-align: center;
    font-size: 12px;
    width: 100%;
}
.share-table td,.hash-table td, .share-table th,.hash-table th
{
    border: 1px solid #cad9ea;
    color: #666;
    height: 30px;
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    white-space: nowrap;
    padding:5px;
}
.share-table thead th,.hash-table thead th
{
    background-color: #CCE8EB;
    
}
.share-table tr:nth-child(odd),.hash-table tr:nth-child(odd)
{
    background: #fff;
}
.share-table tr:nth-child(even),.hash-table tr:nth-child(even)
{
    background: #F5FAFA;
}
.hash-table {
    table-layout: fixed;
}
.hash-table th:first-child {
    width: 5%;
}
.hash-table th:nth-child(2) {
    width: 5%;
}
.hash-table th:nth-child(4) {
    width: 5%;
}
.Moniter-detail .switcher {
    margin-bottom: 20px;
    border:1px solid rgb(202, 217, 234);
    padding: 20px;
    border-radius: 4px;
    position: relative;
}
.Moniter-detail .switcher .moniter-type {
    display: inline-block;
    border:1px solid rgba(27,31,35,0.2);
    background-color:rgb(239, 243, 246);
    padding:10px;
    margin-right: 10px;
    cursor: pointer;
    border-radius: 4px;
}
.Moniter-detail .switcher .moniter-type.active {
    -webkit-box-shadow: 0px 0px 7px 1px gray inset;
            box-shadow: 0px 0px 7px 1px gray inset;
}
.Moniter-detail .switcher .cycle {
    position: absolute;
    line-height: 86px;
    right:15px;
    top:0;
}
input::-webkit-outer-spin-button,input::-webkit-inner-spin-button{
    -webkit-appearance:textfield;
}
.Moniter-detail .switcher .cycleTime {
    -moz-appearance:textfield;
    border:1px solid #333;
    border-radius: 4px;
    line-height: 30px;
    padding:5px;
    margin:0 5px;
    text-align: right;
    width:5em;
}


