.tranning-container {
	background: white;
	padding-top: 70px;
	padding-bottom: 70px;
}
.tranning-container p {
        font-size: 16px;
        color: #333333;
        margin: 1em 20px;
}
.tranning-container a {
	color: #0D9FFF;
    margin-left: 20px;
}
p.mb0 {
    margin-bottom: 0;
}
.tranning-container img {
    margin-left: 20px;
}
.tranning-container h4 {
    text-align: center;
    font-size: 24px;
    color: #333333;
	margin: 1.33em 20px 0;
	font-weight: 700;
}
.tranning-container.statics {
    margin-bottom: 100px;
}
.tranning-container .addIn {
    background: #F8F9FA;
    padding:20px 10px;
}
.tranning-container .addIn .method {
    line-height: 1em;
    margin:0;
}


