.footer {
	text-align: center;
    color: #999;
    text-decoration: none;
    font-size: 14px;
    font-weight: bold;
    padding: 25px;
    line-height: 20px;
    position: fixed;
    border-top: 1px solid #F0F0F0;
    width: 100%;
    bottom: 0;
    left: 0;
    background-color: white;
}
.footer.transparent {
	background-color: transparent;
	border: none;
    position: relative;
    margin-top: 40px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.footer .copy {
	margin-right: 40px;
}
.footer.transparent .copy,.footer.transparent .mail {
	color: white;
}
.footer .mail {
	margin-left: 40px;
	color: #999;

}
.footer .mail:hover {
	color: #f79826;
}


