.home-container {
	width: 1200px;
	margin: 0 auto;
	padding-top: 70px;
}
.tips {
	line-height: 48px;
    background: rgba(0, 0, 0, 0.30);
    border-radius: 40px;
    color: white;
    font-size: 16px;
    color: #FFFFFF;
    width: 1160px;
    margin: 20px 20px 50px;

}
.tips::before {
	display: inline-block;
    content: '';
    background: url(../../imgs/ic_volume_up.png) no-repeat center;
    background-size: 18px;
    width: 18px;
    height: 18px;
    vertical-align: text-top;
    margin: 0 20px;
}
.dashboard {
	width: 100%;
	text-align: center;
	margin-bottom: 20px;
}
.dashboard>div {
	display: inline-block;
	width: 140px;
	height: 100px;
	color: white;
	position: relative;
}
.dashboard>div>div {
	position: absolute;
	width: 100%;
	top: 50%;
	-webkit-transform: translateY(-50%);
	    -ms-transform: translateY(-50%);
	        transform: translateY(-50%);
}
.dashboard h1{
	font-size: 20px;
    letter-spacing: 0.24px;
    font-weight: 700;
}
.dashboard h3 {
	font-size: 12px;
    letter-spacing: 0.17px;
}
.e-charts {
	margin: 0 auto;
}
.info {
	color: white;
}
.info-row {
	text-align: center;
}
.info-row span{
	display: inline-block;
	text-align: left;
	width: 268px;
}
.info-row span:last-child {
	margin-left: 100px;
}
.col-container {
	width: 75%;
	margin: 20px auto 0;
	
}
.col-container>div {
	border-left: 1px solid white;
	float: left;
	width: 33.3%;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	padding: 0px 30px;
}
.col-container>div:last-child {
	border-right: 1px solid white;
}
.workerContainer, .paymentsContainer {
    min-height:552px;
}


