.backMask {
    position: absolute;
    left: 21px;
    top: 50px;
    right: 21px;
    bottom:1px;
    background-color: white;
    display: none;
}
.backMask.shown{
    display: block;
    -webkit-animation: slideIn .1s linear;
            animation: slideIn .1s linear;
}

@-webkit-keyframes slideIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes slideIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.windows8 {
	position: absolute;
	width: 40px;
	height:40px;
    margin:auto;
    left:50%;
    top:50%;
    -webkit-transform: translate(-20px,-20px);
        -ms-transform: translate(-20px,-20px);
            transform: translate(-20px,-20px);
}

.windows8 .wBall {
	position: absolute;
	width: 38px;
	height: 38px;
	opacity: 0;
	transform: rotate(225deg);
		-o-transform: rotate(225deg);
		-ms-transform: rotate(225deg);
		-webkit-transform: rotate(225deg);
		-moz-transform: rotate(225deg);
	    animation: orbit 4.2325s infinite;
		-o-animation: orbit 4.2325s infinite;
		-ms-animation: orbit 4.2325s infinite;
		-webkit-animation: orbit 4.2325s infinite;
		-moz-animation: orbit 4.2325s infinite;
}

.windows8 .wBall .wInnerBall{
	position: absolute;
	width: 5px;
	height: 5px;
	background: #0D9FFF;
	left:0px;
	top:0px;
	border-radius: 5px;
}

.windows8 #wBall_1 {
	animation-delay: 0.926s;
		-o-animation-delay: 0.926s;
		-ms-animation-delay: 0.926s;
		-webkit-animation-delay: 0.926s;
		-moz-animation-delay: 0.926s;
}

.windows8 #wBall_2 {
	animation-delay: 0.183s;
		-o-animation-delay: 0.183s;
		-ms-animation-delay: 0.183s;
		-webkit-animation-delay: 0.183s;
		-moz-animation-delay: 0.183s;
}

.windows8 #wBall_3 {
	animation-delay: 0.3665s;
		-o-animation-delay: 0.3665s;
		-ms-animation-delay: 0.3665s;
		-webkit-animation-delay: 0.3665s;
		-moz-animation-delay: 0.3665s;
}

.windows8 #wBall_4 {
	animation-delay: 0.5495s;
		-o-animation-delay: 0.5495s;
		-ms-animation-delay: 0.5495s;
		-webkit-animation-delay: 0.5495s;
		-moz-animation-delay: 0.5495s;
}

.windows8 #wBall_5 {
	animation-delay: 0.743s;
		-o-animation-delay: 0.743s;
		-ms-animation-delay: 0.743s;
		-webkit-animation-delay: 0.743s;
		-moz-animation-delay: 0.743s;
}



@keyframes orbit {
	0% {
		opacity: 1;
		z-index:99;
		-webkit-transform: rotate(180deg);
		        transform: rotate(180deg);
		-webkit-animation-timing-function: ease-out;
		        animation-timing-function: ease-out;
	}

	7% {
		opacity: 1;
		-webkit-transform: rotate(300deg);
		        transform: rotate(300deg);
		-webkit-animation-timing-function: linear;
		        animation-timing-function: linear;
		origin:0%;
	}

	30% {
		opacity: 1;
		-webkit-transform:rotate(410deg);
		        transform:rotate(410deg);
		-webkit-animation-timing-function: ease-in-out;
		        animation-timing-function: ease-in-out;
		origin:7%;
	}

	39% {
		opacity: 1;
		-webkit-transform: rotate(645deg);
		        transform: rotate(645deg);
		-webkit-animation-timing-function: linear;
		        animation-timing-function: linear;
		origin:30%;
	}

	70% {
		opacity: 1;
		-webkit-transform: rotate(770deg);
		        transform: rotate(770deg);
		-webkit-animation-timing-function: ease-out;
		        animation-timing-function: ease-out;
		origin:39%;
	}

	75% {
		opacity: 1;
		-webkit-transform: rotate(900deg);
		        transform: rotate(900deg);
		-webkit-animation-timing-function: ease-out;
		        animation-timing-function: ease-out;
		origin:70%;
	}

	76% {
	opacity: 0;
		-webkit-transform:rotate(900deg);
		        transform:rotate(900deg);
	}

	100% {
	opacity: 0;
		-webkit-transform: rotate(900deg);
		        transform: rotate(900deg);
	}
}

@-webkit-keyframes orbit {
	0% {
		opacity: 1;
		z-index:99;
		-webkit-transform: rotate(180deg);
		-webkit-animation-timing-function: ease-out;
	}

	7% {
		opacity: 1;
		-webkit-transform: rotate(300deg);
		-webkit-animation-timing-function: linear;
		-webkit-origin:0%;
	}

	30% {
		opacity: 1;
		-webkit-transform:rotate(410deg);
		-webkit-animation-timing-function: ease-in-out;
		-webkit-origin:7%;
	}

	39% {
		opacity: 1;
		-webkit-transform: rotate(645deg);
		-webkit-animation-timing-function: linear;
		-webkit-origin:30%;
	}

	70% {
		opacity: 1;
		-webkit-transform: rotate(770deg);
		-webkit-animation-timing-function: ease-out;
		-webkit-origin:39%;
	}

	75% {
		opacity: 1;
		-webkit-transform: rotate(900deg);
		-webkit-animation-timing-function: ease-out;
		-webkit-origin:70%;
	}

	76% {
	opacity: 0;
		-webkit-transform:rotate(900deg);
	}

	100% {
	opacity: 0;
		-webkit-transform: rotate(900deg);
	}
}


