.iter {
    position: absolute;
    right: 0;
    top: 0;
    line-height: 28px;
}

.iter .iterBox{
    border:1px solid black;
    display: inline-block;
    width: 30px;
    height: 30px;
    text-align: center;
    margin: 0 5px;
    cursor: pointer;
}
.iter .iterBox.selected {
    border-color:#0D9FFF;
    color:#0D9FFF;
}
.iter .iterEcc {
    display: inline-block;
    letter-spacing: 5px;
}


