.Faq-container {
	background: white;
	padding-top: 70px;
	padding-bottom: 70px;
}
.faq-banner {
    background: url(../../imgs/faq.jpg) no-repeat center;
    height: 270px;
    background-size: 1920px auto;
    overflow: hidden;
    position: relative;
}
.faq-banner::before {
        content: "";
        position: absolute;
        width: 100%;
        height: 270px;
        background: -webkit-gradient(linear, right top, left top, from(rgba(0, 6, 47, .4)), to(rgba(34, 49, 93, .4)));
        background: -webkit-linear-gradient(right, rgba(0, 6, 47, .4) 0%, rgba(34, 49, 93, .4) 100%);
        background: -o-linear-gradient(right, rgba(0, 6, 47, .4) 0%, rgba(34, 49, 93, .4) 100%);
        background: linear-gradient(270deg, rgba(0, 6, 47, .4) 0%, rgba(34, 49, 93, .4) 100%);
}
.center {
        position: absolute;
        top: 50%;
        left: 50%;
        -webkit-transform: translate(-50%, -50%);
            -ms-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
        text-align: center;
}
.faq-banner .center h4 {
    color: white;
    font-size: 36px;
    margin: 0;
    line-height: 50px;
    text-align: center;
    font-weight: normal;
}
.faq-banner .center p,
.faq-banner .center a {
    text-align: center;
    font-size: 16px;
    color: white;
}
.faq-wrap {
	padding-top: 20px;
}
.faq-wrap h4 {

        font-size: 18px;
        color: #333333;
        margin-bottom: 0;
    	padding: 2px 24px 0;
    	margin-top: 1.33em;
    	font-weight: 700;
    }

    .faq-wrap p {

        font-size: 16px;
        color: #999999;
        margin: 0;
   		padding: 0 24px 20px;
    }


