body {
	/* background: transparent; */
}
.blocks-container {
	padding-top: 70px;
	background-color: white;
	padding-bottom: 70px;
}
.blockList {
	margin: 20px 0 0;
	/* width: 1200px; */
	/* table-layout: fixed; */
}
.blockList th,.blockList td {
	padding-left: 24px;
    padding-right: 24px;
    text-align: left;
    height: 50px;
    font-size: 16px;
    white-space: nowrap;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    
}
.blockList td {
	font-size: 16px;
}
.blockList tbody tr:nth-of-type(odd) {
	background-color: #F8F9FA;
}
.blockList thead th:nth-child(1) {
	/* display: inline; */
}
.blockList thead th:nth-child(2),.blockList tbody td:nth-child(2) a{
	width: 480px;
}
.blockList thead th:nth-child(3),.blockList tbody td:nth-child(3) a{
	width: 150px;
}
.blockList thead th:nth-child(4),.blockList tbody td:nth-child(4) a{
	width: 324px;
}
.blockList a {
	color: #0D9FFF;
	display: block;
	white-space: nowrap;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    overflow: hidden;
}
.extendBox {
	text-align: center;
}
.more {
	border: 1px solid #0D9FFF;
	border-radius: 4px;
	width: 130px;
	height: 45px;
	display: inline-block;
	margin:20px 0;
	line-height: 45px;
	color: #0D9FFF;
	cursor: pointer;
	
}


