.Header {
	position: fixed;
	top: 0;
	width: 100%;
	z-index: 50;
}
.Header.black {
	background-color: white;
	-webkit-box-shadow: 0 2px 4px 0 rgba(224,224,224,0.50);
	        box-shadow: 0 2px 4px 0 rgba(224,224,224,0.50);
}
.Header.noShadow {
	-webkit-box-shadow: none;
	        box-shadow: none;
}
.Header.faq-bottom {
	border-bottom: 1px solid #f0f0f0;
}
.container {
	width: 1200px;
	margin: 0 auto;
	overflow: hidden;
}
.logo {
	float: left;
}
.logo-img {
	display: inline-block;
	background: url('../../imgs/youyikuangchi_white.svg') no-repeat center;
	background-size: 133px 70px;
	width: 133px;
	height: 70px;
	vertical-align: middle;
	margin-left: 20px;
}
.logo-img.blue {
	background: url('../../imgs/youyikuangchi_blue.svg') no-repeat center;
}
.navbar {
	float: right;
	margin-right: 20px;
}
.navbar li {
	float: left;
}
.navbar li a {
	font-size: 16px;
	line-height: 16px;
    font-weight: bold;
    background: inherit;
    color: white;
    padding: 27px 20px;
    display: inline-block;
    vertical-align: middle;
}
.navbar li a:hover {
	border-bottom: 1px solid #0D9FFF;
}
.navbar li a.black {
	color: #4A4A4A;
}
.navbar li a.active {
	border-bottom: 1px solid #0D9FFF;
}
html,body {
	
}
body {
	background: url(../../imgs/map.png) no-repeat center, -webkit-gradient(linear, left top, left bottom, color-stop(0, #22315D), to(#00052b));
	background: url(../../imgs/map.png) no-repeat center, -webkit-linear-gradient(top, #22315D 0, #00052b 100%);
	background: url(../../imgs/map.png) no-repeat center, -o-linear-gradient(top, #22315D 0, #00052b 100%);
	background: url(../../imgs/map.png) no-repeat center, linear-gradient(180deg, #22315D 0, #00052b 100%);

}


