.miners-container .charts-container {
    height: 448px;
    padding-bottom: 64px;
    background-color: #1F1F3D;
    position: relative;
}
.workerContainer .container,.paymentsContainer .container {
    position: relative;
}
.charts-title {
    color: white;
    font-size: 18px;
    margin: 20px 20px 0;
}

.paymentsStats {
    position: absolute;
    bottom: 20px;
    color: white;
    left: 50%;
    -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
            transform: translateX(-50%);
}

.paymentsStats>div {
    float: left;
    text-align: center;
    padding: 0 30px;
}

.paymentsStats>div {
    border-right: 1px solid white;
}

.paymentsStats>div:last-child {
    border-right: 0px;
}

.paymentsStats h1 {
    font-size: 16px;
    letter-spacing: 5px;
}

.paymentsStats p {
    font-size: 18px;
    margin: 10px 0;
    font-weight: 400;
}

.tipBox {
    padding: 40px 0;
    text-align: center;
    color: lightgray;
}

.table-title {
    font-size: 16px;
    color: #333333;
    padding: 0;
    line-height: 1.5;
    margin: 20px auto 0;
    position: relative;
}

.table-title a {
    display: inline-block;
    padding: 13px;
    background-color: #f5f7f8;
    color: #999;
    cursor: pointer;
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
    border: 1px solid #f5f7f8;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border-bottom: 1px solid #ddd;
}

.table-title a.active {
    background-color: white;
    color: #34495e;
    border: 1px solid #ddd;
    border-bottom: 0px;
}

.container.tab {
    margin: 0 auto -1px;
    width: 1160px;
}

.workersList {
    width: 1160px;
    margin: 0 auto;
    border: 1px solid #ddd;
}

.workersList th,
.workersList td {
    padding-left: 24px;
    padding-right: 24px;
    text-align: left;
    height: 50px;
    font-size: 16px;
    white-space: nowrap;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    line-height: 50px;
}

.workersList td {
    font-size: 16px;
}

.wrapper {
    padding-bottom: 20px;
}

.workersList tbody tr:nth-of-type(odd) {
    background-color: #F8F9FA;
}

.workersList a {
    color: #0D9FFF;
}

.workersList .offline {
    color: red;
}

.paymentsList {
    width: 1160px;
    margin: 0 auto;
    border: 1px solid #ddd;
}
.paymentsList th,
.paymentsList td {
    padding-left: 24px;
    padding-right: 24px;
    text-align: left;
    height: 50px;
    font-size: 16px;
    white-space: nowrap;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    line-height: 50px;
}

.paymentsList td {
    font-size: 16px;
}

.paymentsList tbody tr:nth-of-type(odd) {
    background-color: #F8F9FA;
}

.paymentsList a {
    color: #0D9FFF;
}

.paymentsList tbody td:first-child {
    width: 600px;
}


